.calculation_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5% 10%;
  background: #40d3dc;
  border-bottom: 10px solid black;
  border-bottom-left-radius: 80%;
  border-bottom-right-radius: 45%;
}
.calculation_wrapper .steps_block h3 {
  font-size: 34px;
  margin-bottom: 50px;
  color: #fff;
}
.calculation_wrapper .steps_block .steps_list {
  display: flex;
}
.calculation_wrapper .steps_block .steps_list li {
  list-style: none;
  color: #fff;
  font-size: 20px;
  display: flex;
}
.calculation_wrapper .steps_block .steps_list li span {
  font-size: 40px;
  color: #111;
  margin-right: 20px;
  display: block;
  font-weight: bold;
}
.calculation_wrapper .steps_block .steps_list li:not(:first-child) {
  margin-left: 50px;
}
@keyframes glowing {
  0% {
    background-color: #2ba805;
    box-shadow: 0 0 3px #2ba805;
  }
  50% {
    background-color: #49e819;
    box-shadow: 0 0 10px #49e819;
  }
  100% {
    background-color: #2ba805;
    box-shadow: 0 0 3px #2ba805;
  }
}
.calculation_wrapper .get_started button {
  background-color: #fff;
  border: 0;
  padding: 20px;
  font-size: 30px;
  color: #fff;
  border-radius: 10px;
  font-weight: bold;
  cursor: pointer;
  animation: glowing 1300ms infinite;
  outline: none;
}