@keyframes welcome {
  0% {
    margin-top: -100vh;
    opacity: 0;
  }
  100% {
    margin-top: 0;
    opacity: 1;
  }
}
.welcome_block {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../bg_images/welcome.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100vh;
  display: none;
}
.welcome_block .welcome_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  color: #fff;
  width: 100%;
  height: 100%;
}
.welcome_block .welcome_wrapper h1 {
  font-weight: 600;
  font-size: 2.5em;
  transition: 1s;
  animation: welcome 2s;
}
.welcome_block .welcome_wrapper p {
  font-weight: 800;
  font-size: 1.5em;
  margin-bottom: 20px;
  transition: 1s;
  animation: welcome 2s;
}
.welcome_block .welcome_wrapper .welcome_btn {
  background: linear-gradient(90deg, #5da4c7 24%, #40d3dc 100%);
  border: 0;
  padding: 5px 0;
  outline: none;
  border-radius: 4px;
  width: 20%;
  cursor: pointer;
  margin: 0 auto;
  color: #fff;
  transition: 1s;
  animation: welcome 2s;
}