.burger {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 10%;
  z-index: 999;
}
.burger input {
  width: 100%;
  height: 100%;
}
.burger label {
  width: 100%;
  height: 100%;
}
.burger input[type=checked]:checked ~ .navigation {
  top: 0;
}
.burger .burger_line {
  width: 40px;
  border-radius: 4px;
  height: 6px;
  margin: 0 auto;
  background-color: #40dedc;
  transition: 1s;
}
.burger .burger_line:nth-child(1) {
  position: absolute;
  left: 50%;
  top: 25%;
  transform: translate(-50%, -25%);
}
.burger .burger_line:nth-child(2) {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.burger .burger_line:nth-child(3) {
  position: absolute;
  top: 75%;
  left: 50%;
  transform: translate(-50%, -75%);
}
.burger .burger_line:nth-child(2).close {
  transition: 0.3s;
  transform: translateX(-50px);
  opacity: 0;
}
.burger .burger_line:nth-child(1).close {
  transform: rotate(45deg);
  top: 50%;
  transition: 0.5s;
}
.burger .burger_line:nth-child(3).close {
  transform: rotate(-45deg);
  top: 50%;
  transition: 0.5s;
}